<template>
  <div>feidian</div>
</template>
<script>
import { mapState } from "vuex";

export default {
  name: "feidian",
  computed: {
    ...mapState(["tab"])
  },
  created() {},
  activated() {
    if (this.tab !== "feidian") {
      this.$store.commit("changeTab", "feidian");
    }
  }
};
</script>



